import { useEffect, useState } from 'react';
import vkConnect from '@vkontakte/vk-connect';
let initialState = {
    bottom: null,
    top: null,
    left: null,
    right: null
};
function resolveInsets(e) {
    const { type, data } = e.detail;
    switch (type) {
        case 'VKWebAppUpdateConfig':
        case 'VKWebAppUpdateInsets': //   vk-connect
            const { insets } = data;
            if (insets) {
                return {
                    ...insets,
                    bottom: insets.bottom > 150 ? 0 : insets.bottom //   150          ,     
                };
            }
    }
    return null;
}
vkConnect.subscribe((e) => {
    const insets = resolveInsets(e);
    if (insets) {
        initialState = insets;
    }
});
export default function useInsets() {
    const [insets, setInsets] = useState(initialState);
    useEffect(() => {
        function connectListener(e) {
            const insets = resolveInsets(e);
            if (insets) {
                setInsets(insets);
            }
        }
        vkConnect.subscribe(connectListener);
        return () => {
            vkConnect.unsubscribe(connectListener);
        };
    }, []);
    return insets;
}
